<template>
  <div>
    <h2>v-bind</h2>
    <section>
      <div>
        <img
          v-bind:src="imgSrc"
          v-bind:width="logoWidth"
          v-bind:height="logoHeight"
          alt=""
        />
        <img
          v-bind:src="imgSrc2"
          v-bind:width="logoWidth"
          v-bind:height="logoHeight"
          alt=""
        />
      </div>
      <pre>
      v-bind 지시자는 Vue 인스턴스의 데이터와 HTML 요소의 속성을 
      바인딩하는 데 사용됩니다. 
      이를 통해 HTML 요소의 속성값을 동적으로 설정할 수 있습니다. 
      v-bind를 사용하면 Vue 인스턴스의 데이터 속성이 변경될 때 
      HTML 요소의 속성값도 자동으로 업데이트됩니다.
    </pre
      >
    </section>
    <br />
    <hr />
    <label>
      <input
        type="checkbox"
        v-model="toggled"
        :true-value="trueValue"
        :false-value="falseValue"
      />
      {{ toggled }}
    </label>
    <br />
    <hr />
    <input type="radio" v-model="pick" :value="first" />
    <input type="radio" v-model="pick" :value="second" />
    <span>{{ pick }}</span>
    <br />
    <hr />
    <select v-model="selectedNum">
      <option :value="{ number: 123 }">123</option>
    </select>
    <br />
    <hr />
    <div>
      <label
        ><input type="checkbox" v-model="checked" value="화면" />화면</label
      >
      <label
        ><input type="checkbox" v-model="checked" value="냉난방" />냉난방</label
      >
      <label
        ><input
          type="checkbox"
          v-model="checked"
          value="냉장고"
        />냉장/냉동</label
      >
      <p>체크한 상품: {{ checked }}</p>
    </div>
    <br />
    <hr />
    <div>
      <label
        ><input type="radio" v-model="selFruit" :value="fruit1" />딸기</label
      >
      <label
        ><input type="radio" v-model="selFruit" :value="fruit2" />사과</label
      >
      <label
        ><input type="radio" v-model="selFruit" :value="fruit3" />수박</label
      >
      <p>선택한 과일: {{ selFruit }}</p>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      imgSrc: "https://kr.vuejs.org/images/logo.png",
      imgSrc2: "https://github.com/jmk255/source/blob/main/logo.png?raw=true",
      logoHeight: "50",
      logoWidth: "50",
      toggled: "CoC or inSANe?",
      trueValue: "CoC",
      falseValue: "inSANe",
      pick: "",
      first: "The First",
      second: "The Second",
      selectedNum: 0,
      fruit1: "딸기",
      fruit2: "사과",
      fruit3: "수박이박수",
      checked: [],
      selFruit: "",
    };
  },
};
</script>

<style scoped>
img {
  display: block;
}
section {
  display: flex;
}
</style>
