<template>
  <div class="container">
    <div id="justify">
      <h2>V-If Directive</h2>
      <pre>
        <b>v-if </b>: 조건부로 표현식이 true일 때의 값을 반환할 때 블록을 렌더링 함
        <b>v-else </b>: v-if의 else 조건일 때 블록을 렌더링 함
      </pre>
    </div>
    <div class="example1">
      <h1 v-if="bRender">bRender = true > display</h1>
      <h1 v-else>bRender = false > display</h1>
    </div>
    <div class="example2">
      <h1 v-if="type === 'A'">type = A > display</h1>
      <h1 v-else-if="type === 'B'">type = B > display</h1>
      <h1 v-else-if="type === 'C'">type = C > display</h1>
      <h1 v-else>type = not A,B,C > display</h1>
    </div>
    <div class="example3">
      <button v-if="login">로그인</button>
      <button v-else>로그아웃</button>
    </div>
  </div>
  <br>
  <hr>
  <div id="justify2">
      <h2>V-Show Directive</h2>
      <pre>
        <b>v-show </b>: 조건부로 표현식이 true일 때의 값을 반환할 때 블록을 표시함
        <b> </b>  v-if와 달리 렌더링은 항상 되어있으며 display 속성만 변경함
        <b> </b>  자주 변경될 요소라면 v-show, 아니라면 v-if를 사용하는 게 적절함
      </pre>
      <h1 id="theTitle" v-show="showTitle" >쿵!</h1>
    </div>
</template>

<script>
export default {
  data() {
    return {
      bRender: true,
      type: "B",
      login : false,
      showTitle : false,
    };
  },
};
</script>

<style scoped>
pre {
  text-align: left;
}
pre > b {
  display: inline-block;
  width: 80px;
}
</style>
