<template>
  <div id="container">
    <section id="example1">
      <h2>Full Name : {{ fullName }}</h2>
      <input v-model="firstName" placeholder="first name" type="text" />
      <input v-model="lastName" placeholder="last name" type="text" />
    </section>
    <section id="example2">
      <h1>Watch</h1>
      <ul>
        <li>반응형 속성이 변경될 때마다 함수 호출</li>
      </ul>
      <button type="button" @click="changeName">이름 변경</button>
    </section>
  </div>
</template>

<script>
export default {
  data() {
    return {
      firstName: "Romano",
      lastName: "Topaz",
      fullName: "Romano Topaz",
    };
  },
  computed: {
    setFullName() {
      return this.firstName + " " + this.lastName;
    },
  },
  watch: {
    firstName() {
      this.fullName = `${this.firstName} ${this.lastName}`;
    },
    lastName() {
      this.fullName = `${this.firstName} ${this.lastName}`;
    },
  },
};
</script>
