<template>
  <div>
    <button :class="{active : isActive , 'text-aqua' : colorChange}">객체로 바인딩</button>
    <button  :class="[activeClass , colorClass]">배열로 바인딩</button>
    <div :style="styleObject">인라인 스타일</div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      //true = 클래스추가, false = 클래스 제거
      isActive: false,
      colorChange:false,
      activeClass: "isActive",
      colorClass: "text-aqua",
      styleObject:{
        color:"red",
        backgroundColor:"skyblue",
        width:"300px",
        height:"300px",
      }
    };
  },
};
</script>

<style scoped>
button {
  width: 150px;
  height: 40px;
  border: 0;
  outline: 0;
  background-color: tomato;
  color: #fff;
}
.active {
  display: none;
}
.text-aqua {
  color: aqua;
}
</style>
