<template>
  <div class="container">
    <section id="define">
      <h1>클릭 이벤트(v-on:click)</h1>
      <p>v-on:click = "메서드" 혹은 @click="메서드"</p>
      <button type="button" @click="increaseCounter">Click the Button</button>
      <P>You Click {{ counter }} times</P>
    </section>
    <br>
    <hr>
    <section id="example1">
      <button v-if="bRender" @click="getLogInOut(bRender)">로그인</button>
      <button v-else @click="getLogInOut(bRender) , increaseCounter()">로그아웃</button>
    </section>
    <br>
    <hr>
    <section id="example2">
      <select v-model="selectedValue" @change="changeSelection">
        <option disabled value="Choose Your City"><b>Choose Your City</b></option>
        <option value="Seoul">Seoul</option>
        <option value="Ansan">Ansan</option>
        <option value="Incheon">Incheon</option>
      </select>
    </section>
    <section id="eventList">
      <ol>
        <h3>Popular Event Trigger List</h3>
        <li>.enter</li>
        <li>.tab</li>
        <li>.delete</li>
        <li>.esc</li>
        <li>.space</li>
        <li>.up/down/left/right</li>
      </ol>
    </section>
  </div>
</template>

<script>
export default {
  data() {
    return {
      counter: 0,
      bRender : true,
      strName : '',
      selectedValue : "Choose Your City",
    };
  },
  methods: {
    increaseCounter() {
      this.counter++;
    },
    getLogInOut(bLog){
      this.bRender = bLog? false:true;
      if(this.bRender){
        alert(`안녕히 가세요 ${this.strName} 님!`);
        this.strName = '';
      }
      else{
        this.strName = prompt("안녕하세요, 이름을 알려주세요.");
        alert(`안녕하세요 ${this.strName} 님!`);
      }
    },
    changeSelection(){
      alert(this.selectedValue);
    },
  },
};
</script>

<style scoped>
#example1>button {
  width: 150px;
  height: 40px;
  border:0;
  outline:0;
  background-color: tomato;
  color:#fff;
}
#example1>button:hover{
  background-color:turquoise;
  color:#333;
}
#example2>select{
  width: 200px; height: 30px;
  line-height: 30px;
}
</style>
