<template>
  <div>
    <a @click.stop="doStop">이벤트 전파 중지</a>
    <form @submit.prevent="onSubmit">
      <input type="submit" value="전송 버튼" />
    </form>
    <br>
    <hr>
    <a @click.stop.prevent="doStop">{{clickEl}}</a>
    <form @submit.prevent>아무것도 적지 않아도 가능</form>
    <br>
    <hr>
    <div id="strBox" @click.self="doStr">
      <button type="button" @click="changeStr">문자열 바꾸기</button>
      <p>원래 button을 누르면 strBox div의 클릭 메서드도 동작해야 하지만</p>
      <p>click.self일 때만</p>
    </div>
    <br>
    <hr>
    <pre>
      <h1>addEventListner 메서드 옵션</h1>
      캡쳐모드 @click.capture : 내부 요소 클릭 이벤트 핸들러가 실해되기 전 먼저 실행시키기
      일회용 모드 @click.once : 클릭 이벤트를 1회성으로 만들기
      강제 스크롤 모드 @scroll.passive : preventDefault여도 스크롤이 동작

      <h2>exact</h2>
      <button type="button" @click.exact="strBox">조건을 정확히 만족해야 동작합니다.</button>
    </pre>
  </div>
</template>

<script>
export default {
  data(){
    return{
      clickEl : "",
    }
  },
  methods:{
    doStr(){
      this.clickEl = "self";
    },
    changeStr(){
      this.clickEl = "button";
    }
  }
};
</script>

<style scoped>
  div#strBox{
    padding: 50px;
    background-color: pink;
    border: 2px solid hotpink;
  }
</style>
